import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
import Main from '@/components/Main'
import Index from '@/components/Index'
import Products from '@/components/Products'
import ShoppingCart from '@/components/ShoppingCart'
import ShoppingCartLogin from '@/components/ShoppingCartLogin'
import Login from '@/components/Login'
import Member from '@/components/Member'
import ConfirmOrder from '@/components/ConfirmOrder'
import Details from '@/components/Details'
import NotFound from '@/components/NotFound'

Vue.use(Router)

const router =  new Router({
  routes: [
    {
      path: '',
      component: Main,
      children: [
        {
          path: '/',
          name: 'index',
          component: Index,
          meta:{
            title:''
          }
        },
        {
          path: '/pro',
          name: 'products',
          component: Products,
          meta:{
            title:''
          }
        },
        {
          path: '/pro/:cid',
          name: 'products-c',
          component: Products,
          meta:{
            title:''
          }
        },
        {
          path: '/cart',
          name: 'cart',
          component: ShoppingCart,
          meta:{
            title:''
          }
        },
        {
          path: '/c2l',
          name: 'cart2login',
          component: ShoppingCartLogin
        },
        {
          path: '/member',
          name: 'member',
          component: Member,
          meta:{
            title:''
          }
        }
      ]
    },
    {
      path: '/cforder',
      name: 'cforder',
      component: ConfirmOrder,
      meta:{
        title:''
      }
    },
    {
      path: '/cforder/:id',
      name: 'buynow',
      component: ConfirmOrder,
      meta:{
        title:''
      }
    },
    {
      path: '/details/:id',
      name: 'details',
      component: Details,
      meta:{
        title:''
      },
      beforeEnter(to,from,next){
        let urlArr= store.getters.urlArr;
        let url = from.fullPath;
        let i = -1,
            j = 0;        
        do{
          i=url.indexOf('/',i+1);
          if(i==-1){
            break;
          }else{
            j++;
          }
        }while(true)

        if(j==1&&!/\?/.test(url)){
          urlArr=[];
        }
        urlArr.push(url);
        // console.log(urlArr);
        
        store.dispatch('ModifyDetailsBackPath',urlArr);
        next();
      }

    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta:{
        title:''
      }
    },
    {
      path: '*',
      name: 'notfound',
      component: NotFound,
      meta:{
        title:'NotFound Page'
      }
    }
  ]
});

router.beforeEach((to,from,next)=>{
  // to: Route: , 
  // from: Route: 
  // next: Function:  resolve .  next 

  // title
  document.title = to.meta.title;

  const nextRoute = ['cart','cforder','buynow','member'];

  store.dispatch('CheckLoginState').then(()=>{
    // , nextRoute, login
    if(nextRoute.indexOf(to.name) >= 0){
      if(!store.getters.isLoggedIn){
        let url = '';
        to.name=='buynow'?(url=from.fullPath):(url=to.fullPath);
        next({
          path: '/login',
          query: {redirect: url} //// path
        })
      }else{
        next();
      }  
    }else{
      next();
    }

    // , login, 
    if(to.name === 'login'){
      if(store.getters.isLoggedIn){
        router.push(from.fullPath);
      }
    }
  });  
});

export default router;



